import React, { useState } from "react";

import { Link, graphql, useStaticQuery } from "gatsby";

import Header from "./header";
import SocialLinks from "./social_links";


export default ({ location }) => {
  const data=useStaticQuery(graphql`
    query {
      categories: allDirectory(
        filter: {
          relativeDirectory: { eq: "categories" }
        }
      ) {
        edges {
          node {
            name
          }
        }
      }
    }
  `);
  const isGalleryPage = location.pathname.includes('/gallery');
  const categoriesOrder = {
    '': 10,
    '': 20,
    '': 30,
    'Love story': 40,
    '': 50,
    'Party': 60,
    'Other': 70,
  };
  const [showMenu, setShowMenu] = useState(false);
  const [showSubmenu, setShowSubmenu] = useState(isGalleryPage);
  const item = (text, path, inner) => (
    <li key={path} className={location.pathname === path ? 'current' : ''}>
      <Link {...((inner && !showSubmenu) ? { tabIndex: -1 } : {})} to={path}>{text}</Link>
    </li>
  );

  data.categories.edges.sort(
    (a, b) => (categoriesOrder[a.node.name] || 1000) - (categoriesOrder[b.node.name] || 1000)
  );

  return (
    <aside className="side">
      <Header onBurgerClick={e => { setShowMenu(!showMenu); e.preventDefault(); } } />
      <div className="side-social-links text-center">
        <SocialLinks />
      </div>
      <nav className={ showMenu ? "opened" : ''}>
        <div className="gradient-top"></div>
        <div className="menu-wrapper">
          <ul className="menu">
            {item('', '/')}
            <li className={location.pathname.includes('/gallery') ? 'current' : ''}>
              <a href="" onClick={e => { setShowSubmenu(!showSubmenu); e.preventDefault(); }} className="sub"></a>
              <ul className={showSubmenu ? 'sub-opened' : ''}>
                {data.categories.edges.map(({ node: { name } }) => item(name, `/gallery/${encodeURIComponent(name)}/`, true))}
              </ul>
            </li>
            {[
              ['', '/contact/'],
              ['', '/prices/'],
              ['', '/blog/'],
              [' ', '/studios/'],
            ].map(arr => item(...arr))}
          </ul>
        </div>
        <div className="gradient"></div>
      </nav>
    </aside>
  );
};
