import React from "react";

import { graphql, Link, useStaticQuery } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image"

import Bars from "../svg/bars.svg";

export default ({ onBurgerClick }) => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "logo.png" }) {
        childImageSharp {
          gatsbyImageData(layout: FIXED, width: 26, height: 26, placeholder: TRACED_SVG)
        }
      }
    }
  `);

  return (
    <header>
      <Link to="/" className="logo">
        <span className="logo-main">
          <span className="logo-text">PHOTOLI</span>
          <GatsbyImage className="logo-image" image={data.file.childImageSharp.gatsbyImageData} alt="O" />
        </span>
        <span className="logo-secondary">I WANNA TAKE YOUR PICTURE</span>
      </Link>
      <a href="#nav" onClick={onBurgerClick} className="burger">
        <Bars width="18" />
      </a>
    </header>
  );
};
